// Audio Validation Utilities
export const validateAudioBlob = (blob) => {
    if (!blob || !(blob instanceof Blob)) {
        return false;
    }
    if (blob.size === 0) {
        return false;
    }
    if (!blob.type.startsWith('audio/')) {
        return false;
    }
    return true;
};
export const isValidAudioFile = (file) => {
    if (!file || !(file instanceof File)) {
        return false;
    }
    if (file.size === 0) {
        return false;
    }
    // Prfe auf untersttzte Audio-Formate
    const supportedTypes = [
        'audio/mpeg',
        'audio/mp3',
        'audio/wav',
        'audio/ogg',
        'audio/m4a',
        'audio/aac',
        'audio/webm'
    ];
    if (!supportedTypes.includes(file.type)) {
        return false;
    }
    // Prfe Dateigre (max 50MB)
    const maxSize = 50 * 1024 * 1024; // 50MB
    if (file.size > maxSize) {
        return false;
    }
    return true;
};
export const validateAudioFile = (file) => {
    if (!file || !(file instanceof File)) {
        return { isValid: false, error: 'Keine gltige Datei ausgewhlt' };
    }
    if (file.size === 0) {
        return { isValid: false, error: 'Die Datei ist leer' };
    }
    // Prfe auf untersttzte Audio-Formate
    const supportedTypes = [
        'audio/mpeg',
        'audio/mp3',
        'audio/wav',
        'audio/ogg',
        'audio/m4a',
        'audio/aac',
        'audio/webm'
    ];
    if (!supportedTypes.includes(file.type)) {
        return {
            isValid: false,
            error: `Nicht untersttztes Audio-Format. Untersttzte Formate: ${supportedTypes.join(', ')}`
        };
    }
    // Prfe Dateigre (max 50MB)
    const maxSize = 50 * 1024 * 1024; // 50MB
    if (file.size > maxSize) {
        return {
            isValid: false,
            error: `Datei zu gro. Maximum: ${Math.round(maxSize / (1024 * 1024))}MB`
        };
    }
    return { isValid: true };
};
export const repairAudioBlob = (blob) => {
    if (!validateAudioBlob(blob)) {
        return null;
    }
    // Einfache Reparatur: Blob als Audio/MP3 markieren falls Typ fehlt
    if (!blob.type || blob.type === 'application/octet-stream') {
        return new Blob([blob], { type: 'audio/mpeg' });
    }
    return blob;
};
