// Aural - Voice Social Network
// Specialized WaveSurfer Logging System
import { debugLogger, logDebug, logInfo, logWarn, logError, logCritical, startTimer, endTimer } from './debugLogger';
class WaveSurferLogger {
    state = {
        isCreated: false,
        isReady: false,
        isPlaying: false,
        isPaused: false,
        isDestroyed: false,
        currentTime: 0,
        duration: 0,
        volume: 1,
        playbackRate: 1,
        zoom: 1,
        regions: [],
        plugins: []
    };
    metrics = {
        creationTime: 0,
        errorCount: 0,
        warningCount: 0,
        retryCount: 0
    };
    audioBlobInfo = null;
    containerElement = null;
    wavesurferInstance = null;
    setContainer(element) {
        this.containerElement = element;
        logInfo('wavesurfer', 'Container element set', {
            element: element ? {
                tagName: element.tagName,
                id: element.id,
                className: element.className,
                clientWidth: element.clientWidth,
                clientHeight: element.clientHeight,
                offsetWidth: element.offsetWidth,
                offsetHeight: element.offsetHeight
            } : null
        }, 'WaveSurferLogger', 'setContainer');
    }
    setAudioBlob(blob) {
        if (blob) {
            this.audioBlobInfo = this.analyzeAudioBlob(blob);
            logInfo('blob', 'Audio blob analyzed', this.audioBlobInfo, 'WaveSurferLogger', 'setAudioBlob');
        }
        else {
            this.audioBlobInfo = null;
            logInfo('blob', 'Audio blob cleared', null, 'WaveSurferLogger', 'setAudioBlob');
        }
    }
    analyzeAudioBlob(blob) {
        const info = {
            size: blob.size,
            type: blob.type,
            lastModified: blob.lastModified,
            isCorrupted: false,
            isValid: false
        };
        // Basic validation
        if (blob.size === 0) {
            info.isCorrupted = true;
            logError('blob', 'Audio blob is empty', info, 'WaveSurferLogger', 'analyzeAudioBlob');
            return info;
        }
        if (!blob.type.startsWith('audio/')) {
            info.isCorrupted = true;
            logWarn('blob', 'Audio blob has invalid MIME type', info, 'WaveSurferLogger', 'analyzeAudioBlob');
        }
        // Try to get more info using AudioContext
        this.analyzeAudioBlobAdvanced(blob, info);
        return info;
    }
    async analyzeAudioBlobAdvanced(blob, info) {
        try {
            const arrayBuffer = await blob.arrayBuffer();
            const audioContext = new (window.AudioContext || window.webkitAudioContext)();
            const audioBuffer = await audioContext.decodeAudioData(arrayBuffer);
            info.duration = audioBuffer.duration;
            info.sampleRate = audioBuffer.sampleRate;
            info.channels = audioBuffer.numberOfChannels;
            info.bitRate = Math.round((blob.size * 8) / audioBuffer.duration);
            info.isValid = true;
            logInfo('audio', 'Audio blob analysis completed', info, 'WaveSurferLogger', 'analyzeAudioBlobAdvanced');
            audioContext.close();
        }
        catch (error) {
            info.isCorrupted = true;
            info.isValid = false;
            logError('audio', 'Audio blob analysis failed', {
                error: error.message,
                blobInfo: info
            }, 'WaveSurferLogger', 'analyzeAudioBlobAdvanced');
        }
    }
    logWaveSurferCreation(options) {
        startTimer('wavesurfer-creation');
        this.metrics.creationTime = performance.now();
        logInfo('wavesurfer', 'WaveSurfer creation started', {
            options: this.sanitizeOptions(options),
            container: this.containerElement ? {
                tagName: this.containerElement.tagName,
                dimensions: {
                    width: this.containerElement.clientWidth,
                    height: this.containerElement.clientHeight
                }
            } : null,
            audioBlob: this.audioBlobInfo,
            browserInfo: this.getBrowserInfo(),
            deviceInfo: this.getDeviceInfo()
        }, 'WaveSurferLogger', 'logWaveSurferCreation');
    }
    logWaveSurferCreated(instance) {
        this.wavesurferInstance = instance;
        this.state.isCreated = true;
        endTimer('wavesurfer-creation', {
            instanceCreated: true,
            hasInstance: !!instance
        });
        logInfo('wavesurfer', 'WaveSurfer instance created', {
            hasInstance: !!instance,
            instanceType: instance ? instance.constructor.name : 'null',
            state: this.state
        }, 'WaveSurferLogger', 'logWaveSurferCreated');
    }
    logWaveSurferReady() {
        this.state.isReady = true;
        this.metrics.loadTime = performance.now() - this.metrics.creationTime;
        logInfo('wavesurfer', 'WaveSurfer is ready', {
            loadTime: this.metrics.loadTime,
            state: this.state,
            audioBlob: this.audioBlobInfo
        }, 'WaveSurferLogger', 'logWaveSurferReady');
    }
    logWaveSurferError(error, context) {
        this.metrics.errorCount++;
        logError('wavesurfer', `WaveSurfer error in ${context}`, {
            error: {
                name: error.name,
                message: error.message,
                stack: error.stack
            },
            context,
            state: this.state,
            audioBlob: this.audioBlobInfo,
            container: this.containerElement ? {
                tagName: this.containerElement.tagName,
                dimensions: {
                    width: this.containerElement.clientWidth,
                    height: this.containerElement.clientHeight
                }
            } : null,
            metrics: this.metrics
        }, 'WaveSurferLogger', 'logWaveSurferError');
    }
    logWaveSurferWarning(message, data) {
        this.metrics.warningCount++;
        logWarn('wavesurfer', message, {
            data,
            state: this.state,
            audioBlob: this.audioBlobInfo
        }, 'WaveSurferLogger', 'logWaveSurferWarning');
    }
    logWaveSurferEvent(event, data) {
        logDebug('wavesurfer', `WaveSurfer event: ${event}`, {
            data,
            state: this.state,
            timestamp: performance.now()
        }, 'WaveSurferLogger', 'logWaveSurferEvent');
    }
    logWaveSurferStateChange(property, oldValue, newValue) {
        this.state[property] = newValue;
        logDebug('wavesurfer', `State change: ${property}`, {
            property,
            oldValue,
            newValue,
            state: this.state
        }, 'WaveSurferLogger', 'logWaveSurferStateChange');
    }
    logWaveSurferDestroy() {
        this.state.isDestroyed = true;
        this.state.isCreated = false;
        this.state.isReady = false;
        this.wavesurferInstance = null;
        logInfo('wavesurfer', 'WaveSurfer destroyed', {
            finalState: this.state,
            finalMetrics: this.metrics
        }, 'WaveSurferLogger', 'logWaveSurferDestroy');
    }
    logRetryAttempt(attempt, maxAttempts, reason) {
        this.metrics.retryCount++;
        logWarn('wavesurfer', `Retry attempt ${attempt}/${maxAttempts}`, {
            attempt,
            maxAttempts,
            reason,
            state: this.state,
            audioBlob: this.audioBlobInfo
        }, 'WaveSurferLogger', 'logRetryAttempt');
    }
    logPerformanceMetrics() {
        const currentTime = performance.now();
        const totalTime = currentTime - this.metrics.creationTime;
        this.metrics.memoryUsage = this.getMemoryUsage();
        this.metrics.audioContextState = this.getAudioContextState();
        this.metrics.webAudioNodes = this.getWebAudioNodeCount();
        logInfo('performance', 'WaveSurfer performance metrics', {
            metrics: this.metrics,
            totalTime,
            state: this.state,
            audioBlob: this.audioBlobInfo
        }, 'WaveSurferLogger', 'logPerformanceMetrics');
    }
    sanitizeOptions(options) {
        if (!options)
            return options;
        const sanitized = { ...options };
        // Remove sensitive or large data
        delete sanitized.audioContext;
        delete sanitized.mediaElement;
        // Limit array sizes
        if (Array.isArray(sanitized.plugins)) {
            sanitized.plugins = sanitized.plugins.map((plugin) => typeof plugin === 'string' ? plugin : plugin.constructor?.name || 'UnknownPlugin');
        }
        return sanitized;
    }
    getBrowserInfo() {
        return {
            userAgent: navigator.userAgent,
            language: navigator.language,
            platform: navigator.platform,
            cookieEnabled: navigator.cookieEnabled,
            onLine: navigator.onLine,
            hardwareConcurrency: navigator.hardwareConcurrency,
            maxTouchPoints: navigator.maxTouchPoints,
            webAudioSupported: !!(window.AudioContext || window.webkitAudioContext),
            mediaDevicesSupported: !!(navigator.mediaDevices && navigator.mediaDevices.getUserMedia),
            webGLSupported: !!document.createElement('canvas').getContext('webgl')
        };
    }
    getDeviceInfo() {
        return {
            screenWidth: screen.width,
            screenHeight: screen.height,
            windowWidth: window.innerWidth,
            windowHeight: window.innerHeight,
            devicePixelRatio: window.devicePixelRatio,
            colorDepth: screen.colorDepth,
            pixelDepth: screen.pixelDepth,
            isMobile: /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent),
            isTouchDevice: 'ontouchstart' in window,
            connectionType: navigator.connection?.effectiveType || 'unknown'
        };
    }
    getMemoryUsage() {
        if ('memory' in performance) {
            return performance.memory.usedJSHeapSize;
        }
        return undefined;
    }
    getAudioContextState() {
        try {
            if (this.wavesurferInstance && this.wavesurferInstance.backend && this.wavesurferInstance.backend.ac) {
                return this.wavesurferInstance.backend.ac.state;
            }
        }
        catch (error) {
            // Ignore errors
        }
        return undefined;
    }
    getWebAudioNodeCount() {
        try {
            if (this.wavesurferInstance && this.wavesurferInstance.backend && this.wavesurferInstance.backend.ac) {
                // This is a rough estimate - actual implementation would depend on WaveSurfer internals
                return 0; // Placeholder
            }
        }
        catch (error) {
            // Ignore errors
        }
        return undefined;
    }
    getState() {
        return { ...this.state };
    }
    getMetrics() {
        return { ...this.metrics };
    }
    getAudioBlobInfo() {
        return this.audioBlobInfo ? { ...this.audioBlobInfo } : null;
    }
    getSummary() {
        return {
            state: this.state,
            metrics: this.metrics,
            audioBlob: this.audioBlobInfo,
            container: this.containerElement ? {
                tagName: this.containerElement.tagName,
                dimensions: {
                    width: this.containerElement.clientWidth,
                    height: this.containerElement.clientHeight
                }
            } : null,
            browserInfo: this.getBrowserInfo(),
            deviceInfo: this.getDeviceInfo()
        };
    }
    isLoggingEnabled() {
        return debugLogger.isLoggingEnabled();
    }
    reset() {
        this.state = {
            isCreated: false,
            isReady: false,
            isPlaying: false,
            isPaused: false,
            isDestroyed: false,
            currentTime: 0,
            duration: 0,
            volume: 1,
            playbackRate: 1,
            zoom: 1,
            regions: [],
            plugins: []
        };
        this.metrics = {
            creationTime: 0,
            errorCount: 0,
            warningCount: 0,
            retryCount: 0
        };
        this.audioBlobInfo = null;
        this.containerElement = null;
        this.wavesurferInstance = null;
        logInfo('wavesurfer', 'WaveSurfer logger reset', null, 'WaveSurferLogger', 'reset');
    }
}
// Create singleton instance
export const wavesurferLogger = new WaveSurferLogger();
// Re-export debugLogger functions
export { debugLogger, logDebug, logInfo, logWarn, logError, logCritical, startTimer, endTimer } from './debugLogger';
export default wavesurferLogger;
