// Audio URL Manager
export class AudioUrlManager {
    static urlCache = new Map();
    static getAudioUrl(trackId) {
        return this.urlCache.get(trackId) || null;
    }
    static setAudioUrl(trackId, url) {
        this.urlCache.set(trackId, url);
    }
    static async storeAudioUrl(trackId, blob, type = 'blob') {
        try {
            if (type === 'blob') {
                const url = URL.createObjectURL(blob);
                this.setAudioUrl(trackId, url);
                return url;
            }
            else if (type === 'base64') {
                const reader = new FileReader();
                return new Promise((resolve, reject) => {
                    reader.onload = () => {
                        const base64Url = reader.result;
                        this.setAudioUrl(trackId, base64Url);
                        resolve(base64Url);
                    };
                    reader.onerror = reject;
                    reader.readAsDataURL(blob);
                });
            }
            else if (type === 'unique') {
                // Fr einzigartige URLs, erstelle eine spezielle Kennung
                const uniqueId = `aural-audio-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
                const url = URL.createObjectURL(blob);
                this.setAudioUrl(trackId, uniqueId);
                this.setAudioUrl(uniqueId, url); // Speichere auch die tatschliche URL
                return uniqueId;
            }
            throw new Error(`Unsupported type: ${type}`);
        }
        catch (error) {
            console.error('Failed to store audio URL:', error);
            throw error;
        }
    }
    static clearCache() {
        this.urlCache.clear();
    }
    static removeUrl(trackId) {
        this.urlCache.delete(trackId);
    }
    static clearTrackUrls(trackId) {
        this.urlCache.delete(trackId);
    }
    static getUrlInfo(trackId) {
        // Temporary implementation
        return { originalBlob: new Blob() };
    }
    static getUrlStats() {
        // Temporary implementation
        return {};
    }
    static getAudioUrlByUniqueId(uniqueId) {
        return this.urlCache.get(uniqueId) || null;
    }
}
