import { useEffect } from 'react';
import { useFeedStore } from '../stores/feedStore';
import { useUserStore } from '../stores/userStore';
import { centralDB } from '../database/centralDatabase_simple';
export const useDatabaseSync = () => {
    const { setTracks, deleteTracksByUser, tracks: feedTracks } = useFeedStore();
    const { myTracks, clearAllTracksExceptHoller } = useUserStore();
    // Synchronisiere Datenbank mit Stores beim Laden
    useEffect(() => {
        const syncData = () => {
            // WICHTIG: Verwende nur Datenbank-Tracks fr Konsistenz
            const allTracks = centralDB.getAllTracks();
            console.log('useDatabaseSync: Lade Tracks aus Datenbank:', allTracks.length);
            console.log('useDatabaseSync: Tracks:', allTracks.map(t => ({ id: t.id, title: t.title, user: t.user.username })));
            // Setze die synchronisierten Tracks (nur aus Datenbank)
            setTracks(allTracks);
        };
        syncData();
    }, [setTracks]);
    // Funktionen fr Admin-Operationen
    const deleteTrack = (trackId) => {
        console.log('=== HOOK: deleteTrack aufgerufen fr:', trackId);
        const success = centralDB.deleteTrack(trackId);
        if (success) {
            console.log('Track erfolgreich gelscht, synchronisiere...');
            // Synchronisiere nach dem Lschen
            const allTracks = centralDB.getAllTracks();
            console.log('Hook: Setze Tracks nach Lschung:', allTracks.length);
            setTracks(allTracks);
            // Lsche auch aus localStorage fr FeedStore
            localStorage.removeItem('aural-feed-store');
            console.log('FeedStore localStorage gelscht');
            // Zustzlich: Lade die Tracks aus der Datenbank neu
            setTimeout(() => {
                console.log('Hook: Lade Tracks aus Datenbank neu...');
                const { loadTracksFromDatabase } = useFeedStore.getState();
                loadTracksFromDatabase();
            }, 100);
        }
        else {
            console.log('Fehler beim Lschen des Tracks');
        }
        return success;
    };
    const deleteUser = (userId) => {
        const success = centralDB.deleteUser ? centralDB.deleteUser(userId) : false;
        if (success) {
            // Synchronisiere nach dem Lschen
            const allTracks = centralDB.getAllTracks();
            setTracks(allTracks);
        }
        return success;
    };
    const updateTrack = (trackId, updates) => {
        const success = centralDB.updateTrack(trackId, updates);
        if (success) {
            // Synchronisiere nach dem Update
            const allTracks = centralDB.getAllTracks();
            setTracks(allTracks);
        }
        return success;
    };
    const getAllUsers = () => {
        return centralDB.getAllUsers();
    };
    const getAllTracks = () => {
        return centralDB.getAllTracks();
    };
    const getTracksSorted = (sortBy, order = 'desc') => {
        return centralDB.getTracksSorted ? centralDB.getTracksSorted(sortBy, order) : [];
    };
    const searchTracks = (query) => {
        return centralDB.searchTracks ? centralDB.searchTracks(query) : [];
    };
    const getStats = () => {
        return centralDB.getStats();
    };
    const addTrackToDatabase = (track) => {
        console.log('useDatabaseSync: Fge Track zur Datenbank hinzu:', track.id, track.title);
        console.log('useDatabaseSync: Track-Details:', {
            id: track.id,
            title: track.title,
            user: track.user.username,
            url: track.url?.substring(0, 50) + '...',
            fileSize: track.fileSize
        });
        // Erstelle eine Datei-Eintrag fr den Track
        const file = {
            id: `file-${track.id}`,
            filename: track.filename || `${track.title.toLowerCase().replace(/\s+/g, '_')}.wav`,
            path: `https://www.goaural.com/uploads/${track.user.username}/${track.filename || `${track.title.toLowerCase().replace(/\s+/g, '_')}.wav`}`,
            size: track.fileSize || 0,
            uploadedAt: track.createdAt,
            userId: track.user.id
        };
        // Fge Track zur Datenbank hinzu
        centralDB.addTrack(track);
        // WICHTIG: Prfe, ob Track wirklich in der Datenbank ist
        const allTracks = centralDB.getAllTracks();
        const addedTrack = allTracks.find(t => t.id === track.id);
        console.log('useDatabaseSync: Track in Datenbank gefunden:', !!addedTrack);
        console.log('useDatabaseSync: Alle Tracks in Datenbank:', allTracks.length);
        // Synchronisiere FeedStore mit der Datenbank
        setTracks(allTracks);
        console.log('useDatabaseSync: Track zur Datenbank hinzugefgt, FeedStore synchronisiert');
        return true;
    };
    const deleteAllUserContent = () => {
        console.log('=== HOOK: deleteAllUserContent aufgerufen ===');
        // Lsche alle Inhalte aus der Datenbank
        centralDB.deleteAllUserContent();
        // WICHTIG: Lsche ALLE myTracks (auch yevvo's Tracks)
        // Nur Holler die Waldfee Tracks bleiben erhalten
        const hollaUserId = '4';
        const filteredMyTracks = myTracks.filter(track => track.user.id === hollaUserId);
        // Bereinige UserStore - behalte nur Holler-Tracks
        clearAllTracksExceptHoller();
        console.log('UserStore: Vorher myTracks:', myTracks.length);
        console.log('UserStore: Nachher myTracks (nur Holler):', filteredMyTracks.length);
        // WICHTIG: Bereinige auch FeedStore - lsche alle Tracks auer Holler
        // Finde alle Benutzer-IDs auer Holler (aus beiden Stores)
        const allUserIds = [...new Set([
                ...myTracks.map(track => track.user.id),
                ...feedTracks.map(track => track.user.id)
            ])];
        const nonHollaUserIds = allUserIds.filter(id => id !== hollaUserId);
        // Lsche alle Tracks von anderen Benutzern aus dem FeedStore
        nonHollaUserIds.forEach(userId => {
            deleteTracksByUser(userId);
            console.log(`FeedStore: Tracks von Benutzer ${userId} gelscht`);
        });
        // WICHTIG: Lsche auch alle Tracks von anderen Benutzern aus der Datenbank
        // (auer den ersten 3 von Holler, die bereits von deleteAllUserContent behandelt wurden)
        const allDbTracks = centralDB.getAllTracks();
        const nonHollaDbTracks = allDbTracks.filter(track => track.user.id !== hollaUserId);
        nonHollaDbTracks.forEach(track => {
            centralDB.deleteTrack(track.id);
            console.log(`Datenbank: Track ${track.id} von Benutzer ${track.user.id} gelscht`);
        });
        console.log('User Store gefiltert, behalte nur Holler-Tracks:', filteredMyTracks.length);
        console.log('FeedStore bereinigt, alle anderen Benutzer-Tracks (inkl. yevvo) wurden gelscht');
        // Synchronisiere nach dem Lschen
        const allTracks = centralDB.getAllTracks();
        console.log('Hook: Tracks nach Lschung:', allTracks.length);
        // Verwende nur Datenbank-Tracks
        console.log('Hook: Setze Tracks:', allTracks.length);
        setTracks(allTracks);
        // Lsche auch localStorage fr FeedStore
        localStorage.removeItem('aural-feed-store');
        console.log('FeedStore localStorage gelscht');
        // Zustzlich: Lade die Tracks aus der Datenbank neu
        setTimeout(() => {
            console.log('Hook: Lade Tracks aus Datenbank neu...');
            const { loadTracksFromDatabase } = useFeedStore.getState();
            loadTracksFromDatabase();
            // WICHTIG: Erzwinge eine vollstndige Neuinitialisierung des FeedStore
            setTimeout(() => {
                console.log('Hook: Erzwinge vollstndige Neuinitialisierung...');
                const { setTracks } = useFeedStore.getState();
                const freshDbTracks = centralDB.getAllTracks();
                console.log('Hook: Frische DB-Tracks:', freshDbTracks.length);
                setTracks(freshDbTracks);
                // Lsche localStorage komplett und speichere neue Daten
                localStorage.removeItem('aural-feed-store');
                localStorage.setItem('aural-feed-store', JSON.stringify({
                    state: { tracks: freshDbTracks, isLoading: false },
                    version: 0
                }));
                console.log('Hook: FeedStore localStorage komplett neu geschrieben');
            }, 200);
        }, 100);
        return true; // Erfolgreich ausgefhrt
    };
    return {
        deleteTrack,
        deleteUser,
        updateTrack,
        getAllUsers,
        getAllTracks,
        getTracksSorted,
        searchTracks,
        getStats,
        deleteAllUserContent,
        addTrackToDatabase
    };
};
