// Aural - Voice Social Network
// Comprehensive Debug Logging System for WaveSurfer
class DebugLogger {
    logs = [];
    maxLogs = 100; // Reduced from 1000 to 100
    sessionId;
    isEnabled = false; // Disabled by default to prevent performance issues
    performanceMetrics = new Map();
    constructor() {
        this.sessionId = this.generateSessionId();
        this.setupGlobalErrorHandlers();
        this.setupPerformanceMonitoring();
    }
    generateSessionId() {
        return `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
    }
    setupGlobalErrorHandlers() {
        // Global error handler
        window.addEventListener('error', (event) => {
            this.log('error', 'error', 'Global Error', {
                message: event.message,
                filename: event.filename,
                lineno: event.lineno,
                colno: event.colno,
                error: event.error
            });
        });
        // Unhandled promise rejection handler
        window.addEventListener('unhandledrejection', (event) => {
            this.log('error', 'error', 'Unhandled Promise Rejection', {
                reason: event.reason,
                promise: event.promise
            });
        });
        // Console override for better tracking
        this.overrideConsole();
    }
    overrideConsole() {
        const originalConsole = {
            log: console.log,
            warn: console.warn,
            error: console.error,
            info: console.info,
            debug: console.debug
        };
        console.log = (...args) => {
            this.log('debug', 'debug', 'Console Log', { args });
            originalConsole.log(...args);
        };
        console.warn = (...args) => {
            this.log('warn', 'debug', 'Console Warn', { args });
            originalConsole.warn(...args);
        };
        console.error = (...args) => {
            this.log('error', 'debug', 'Console Error', { args });
            originalConsole.error(...args);
        };
        console.info = (...args) => {
            this.log('info', 'debug', 'Console Info', { args });
            originalConsole.info(...args);
        };
        console.debug = (...args) => {
            this.log('debug', 'debug', 'Console Debug', { args });
            originalConsole.debug(...args);
        };
    }
    setupPerformanceMonitoring() {
        // Disabled automatic performance monitoring to prevent performance issues
        // Only monitor when explicitly enabled
        if (this.isEnabled) {
            // Monitor memory usage
            if ('memory' in performance) {
                setInterval(() => {
                    const memory = performance.memory;
                    this.log('debug', 'performance', 'Memory Usage', {
                        usedJSHeapSize: memory.usedJSHeapSize,
                        totalJSHeapSize: memory.totalJSHeapSize,
                        jsHeapSizeLimit: memory.jsHeapSizeLimit
                    });
                }, 30000); // Every 30 seconds instead of 10
            }
            // Monitor Web Audio API state
            this.monitorWebAudioState();
        }
    }
    monitorWebAudioState() {
        // Disabled to prevent performance issues
        // Only monitor when explicitly enabled
        if (this.isEnabled) {
            setInterval(() => {
                try {
                    // Check if AudioContext is available
                    if (window.AudioContext || window.webkitAudioContext) {
                        this.log('debug', 'audio', 'Web Audio State', {
                            audioContextAvailable: true,
                            audioContextState: 'unknown' // Will be updated when context is created
                        });
                    }
                }
                catch (error) {
                    this.log('warn', 'audio', 'Web Audio State Check Failed', { error: error.message });
                }
            }, 30000); // Every 30 seconds instead of 5
        }
    }
    log(level, category, message, data, component, action) {
        if (!this.isEnabled)
            return;
        // Skip debug logs in production to improve performance
        if (level === 'debug' && process.env.NODE_ENV === 'production')
            return;
        const logEntry = {
            timestamp: new Date().toISOString(),
            level,
            category,
            message,
            data: this.sanitizeData(data),
            stack: this.getStackTrace(),
            sessionId: this.sessionId,
            component,
            action
        };
        this.logs.push(logEntry);
        // Keep only the last maxLogs entries
        if (this.logs.length > this.maxLogs) {
            this.logs = this.logs.slice(-this.maxLogs);
        }
        // Store in sessionStorage for persistence
        this.persistLogs();
        // Also log to console with appropriate level
        this.logToConsole(logEntry);
    }
    sanitizeData(data) {
        if (data === null || data === undefined)
            return data;
        try {
            // Create a deep copy and sanitize
            const sanitized = JSON.parse(JSON.stringify(data, (key, value) => {
                // Remove sensitive data
                if (typeof key === 'string' && (key.toLowerCase().includes('password') ||
                    key.toLowerCase().includes('token') ||
                    key.toLowerCase().includes('key') ||
                    key.toLowerCase().includes('secret'))) {
                    return '[REDACTED]';
                }
                // Limit string length
                if (typeof value === 'string' && value.length > 1000) {
                    return value.substring(0, 1000) + '...[TRUNCATED]';
                }
                return value;
            }));
            return sanitized;
        }
        catch (error) {
            return '[DATA_SANITIZATION_FAILED]';
        }
    }
    getStackTrace() {
        const stack = new Error().stack;
        return stack ? stack.split('\n').slice(2, 10).join('\n') : '';
    }
    persistLogs() {
        try {
            const recentLogs = this.logs.slice(-100); // Keep only last 100 logs in storage
            sessionStorage.setItem('aural_debug_logs', JSON.stringify(recentLogs));
        }
        catch (error) {
            // Ignore storage errors
        }
    }
    logToConsole(logEntry) {
        const prefix = `[${logEntry.timestamp}] [${logEntry.level.toUpperCase()}] [${logEntry.category}]`;
        const message = `${prefix} ${logEntry.message}`;
        switch (logEntry.level) {
            case 'debug':
                console.debug(message, logEntry.data);
                break;
            case 'info':
                console.info(message, logEntry.data);
                break;
            case 'warn':
                console.warn(message, logEntry.data);
                break;
            case 'error':
            case 'critical':
                console.error(message, logEntry.data);
                break;
        }
    }
    startPerformanceTimer(name) {
        this.performanceMetrics.set(name, {
            startTime: performance.now(),
            memoryUsage: this.getMemoryUsage()
        });
    }
    endPerformanceTimer(name, additionalData) {
        const metric = this.performanceMetrics.get(name);
        if (metric) {
            metric.endTime = performance.now();
            metric.duration = metric.endTime - metric.startTime;
            metric.memoryUsage = this.getMemoryUsage();
            this.log('info', 'performance', `Performance Timer: ${name}`, {
                duration: metric.duration,
                memoryUsage: metric.memoryUsage,
                ...additionalData
            });
            this.performanceMetrics.delete(name);
        }
    }
    getMemoryUsage() {
        if ('memory' in performance) {
            return performance.memory.usedJSHeapSize;
        }
        return undefined;
    }
    getLogs(filter) {
        let filteredLogs = [...this.logs];
        if (filter) {
            if (filter.level) {
                filteredLogs = filteredLogs.filter(log => log.level === filter.level);
            }
            if (filter.category) {
                filteredLogs = filteredLogs.filter(log => log.category === filter.category);
            }
            if (filter.component) {
                filteredLogs = filteredLogs.filter(log => log.component === filter.component);
            }
            if (filter.since) {
                filteredLogs = filteredLogs.filter(log => new Date(log.timestamp) >= filter.since);
            }
        }
        return filteredLogs;
    }
    getLogsByCategory(category) {
        return this.getLogs({ category });
    }
    getErrorLogs() {
        return this.getLogs({ level: 'error' });
    }
    getCriticalLogs() {
        return this.getLogs({ level: 'critical' });
    }
    clearLogs() {
        this.logs = [];
        sessionStorage.removeItem('aural_debug_logs');
    }
    exportLogs() {
        return JSON.stringify(this.logs, null, 2);
    }
    setEnabled(enabled) {
        this.isEnabled = enabled;
    }
    isLoggingEnabled() {
        return this.isEnabled;
    }
    getSessionId() {
        return this.sessionId;
    }
    getLogCount() {
        return this.logs.length;
    }
    getLogSummary() {
        const summary = {
            total: this.logs.length,
            byLevel: {},
            byCategory: {},
            errors: 0,
            warnings: 0
        };
        // Initialize counters
        const levels = ['debug', 'info', 'warn', 'error', 'critical'];
        const categories = ['wavesurfer', 'audio', 'blob', 'dom', 'network', 'performance', 'error', 'lifecycle'];
        levels.forEach(level => summary.byLevel[level] = 0);
        categories.forEach(category => summary.byCategory[category] = 0);
        // Count logs
        this.logs.forEach(log => {
            summary.byLevel[log.level]++;
            summary.byCategory[log.category]++;
            if (log.level === 'error' || log.level === 'critical') {
                summary.errors++;
            }
            if (log.level === 'warn') {
                summary.warnings++;
            }
        });
        return summary;
    }
}
// Create singleton instance
export const debugLogger = new DebugLogger();
// Export convenience functions
export const logDebug = (category, message, data, component, action) => debugLogger.log('debug', category, message, data, component, action);
export const logInfo = (category, message, data, component, action) => debugLogger.log('info', category, message, data, component, action);
export const logWarn = (category, message, data, component, action) => debugLogger.log('warn', category, message, data, component, action);
export const logError = (category, message, data, component, action) => debugLogger.log('error', category, message, data, component, action);
export const logCritical = (category, message, data, component, action) => debugLogger.log('critical', category, message, data, component, action);
export const startTimer = (name) => debugLogger.startPerformanceTimer(name);
export const endTimer = (name, additionalData) => debugLogger.endPerformanceTimer(name, additionalData);
export default debugLogger;
